import React from 'react';
import '../../dialogs.css';
import DialogAbout from '../dialog-about/dialog-about';

import {NavLink} from "react-router-dom";

const DialogItem = (props) => {
    let path = "/dialogs/" + props.id;

    return (
        <NavLink to={path}><DialogAbout Name={props.Name} Text={props.Text} Prof={props.Prof}/></NavLink>
    )
}



export default DialogItem;