import React from 'react';
import './dialog-about.css';
import prof5 from './../../../../../../assets/img/profile-small-5.jpg';

const DialogAbout = (props) => {

    return (
        <div className='dialogs_item'>
            <div className='friends_ph'>
                <img src={props.Prof}/>
            </div>
            <div className='friends_name'>
                <p className='friends_name_text'>{props.Name}</p>
                <div>
                    <img className='img_name-i' src={prof5}/>
                    <p className='dialogs_text'>{props.text}</p>
                </div>

            </div>
        </div>
    )
}

export default DialogAbout;