import React from 'react';
import './dialogs.css';
import DialogItem from "./dialogs-info/dialogItem/dialogItem";
import {Redirect} from "react-router-dom";
import Login from "../../auth/login";

const Dialogs = (props) => {
    let dialogsItem =
        props.dialogsData.map(dialog => <DialogItem Name={dialog.name} id={dialog.id} Text={dialog.message}
                                                    Prof={dialog.photo}/>);

    if (!props.isAuth) return <Redirect to={"/login"}/>;

    return (
        <div className='col-lg-5'>
            <div className='card card_dialogs'>

                <div className='dialogs'>


                    <input type="text" className="ui_search_field _field" id="im_dialogs_search"
                           autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false"
                           placeholder="Поиск"/>

                    {dialogsItem}

                </div>

            </div>
        </div>
    )
    //   }
    //  }
    //  </StoreContext.Consumer>
}

export default Dialogs;